<template>
  <div class="edit_container">
    <div class="left">
      <div class="number">
        <span></span>
        <el-input v-model="num" placeholder="3"></el-input>
        <el-button type="success" @click="showInputs = true"></el-button>
      </div>
      <div class="inputs" v-if="showInputs">
        <label v-for="i in Number(num)" :key="i">
          <el-input v-model="valus[i-1]" placeholder=""></el-input>
        </label>
      </div>
      <quill-editor
        v-model="content"
        ref="myQuillEditor"
        :options="editorOption"
        @blur="onEditorBlur($event)"
        @focus="onEditorFocus($event)"
        @change="onEditorChange($event)"
      ></quill-editor>
      <el-button class="submit" type="success" @click="saveHtml">JSON</el-button>
      <p class="hint">
        1Wordjson
        <br />2
        <br />3
        <br />4
        <br />
        <span>*2</span>
      </p>
    </div>
    <div class="right">
      <el-input type="textarea" :rows="10" placeholder="JSON" v-model="textarea"></el-input>
    </div>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      content: `<p>hello world</p>`,
      editorOption: {},
      num: "",
      showInputs: false,
      valus: [],
      textarea: "",
      cc: {
        name: "",
        content:
          "537",
        offer:
          " 2020-06-30",
        address: `Via della Moda115069 Serravalle Scrivia (AL)Italy<br>
                Via Marco Polo 130020 Noventa di Piave (VE)Italy<br>
                : Via Meucci snc, 50031 Barberino di Mugello Italy<br>
                : Via Ponte di Piscina Cupa 64 00128 Castel Romano Rome, Italy<br>
                : S.P. 336 Sannitica 81025 Marcianise , Italy`,
        url: "http://www.enlavaguada.com/W/do/centre/inicio"
      }
    };
  },
  computed: {
    editor() {
      return this.$refs.myQuillEditor.quill;
    }
  },
  methods: {
    onEditorReady(editor) {
      // 
    },
    onEditorBlur() {}, // 
    onEditorFocus() {}, // 
    onEditorChange(e) {
      console.log(e);

      this.text = e.text;
    }, // 
    saveHtml(event) {
      // 1
      let text = this.text.replace(/\n\n\n\n\n/g, "<<<<<<>>>>>>>");
      text = this.text.replace(/\n\n\n\n/g, "<<<<<<>>>>>>>");
      text = text.replace(/\n\n\n/g, "<<<<<<>>>>>>>");
      text = text.replace(/\n/g, "thisIsFengGeFu");
      console.log(text);

      let arr = text.split("<<<<<<>>>>>>>");
      console.log(arr);

      // 2
      let arr2 = [];
      arr.forEach(e => {
        let o = {};

        let me = e.split("thisIsFengGeFu");
        console.log(me);

        this.valus.forEach((c, i) => {
          o[c] = me[i];
        });
        arr2.push(o);
      });
      // 3
      this.textarea = JSON.stringify(arr2);
    }
  }
};
</script>

<style lang="less">
.ql-editor {
  min-height: 400px;
  max-height: 100vh;
  overflow: auto;
}
.edit_container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  .left {
    flex: 1;
    min-width: 800px;
    .inputs {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      label {
        padding: 0 10px;
        margin-bottom: 10px;
      }
      .el-input {
        width: 100px;
      }
    }
    .hint {
      font-size: 12px;
      line-height: 20px;
      color: #666;
      span {
        color: #f60;
      }
    }
  }
  .right {
    flex: 1;
    min-width: 300px;
    padding: 10px;
    background: #08f5e470;
    margin-left: 30px;
    .el-input {
      width: 100%;
    }
    textarea {
      height: 100vh;
    }
  }
  .submit {
    display: block;
    margin: 30px auto;
  }
}
.number {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 20px;
  .el-input {
    max-width: 200px;
    margin: 0 30px;
  }
}
</style>


